<template>
	<div class="editor">
		<div class="editor__buttons">
			<button
				class="btn btn-dark"
				@click="cancelEditing"
				title="Добавить фрагмент"
				v-if="isEditing"
			>
				Отмена
			</button>
			<button
				class="btn btn-dark"
				@click="sendText"
				title="Добавить фрагмент"
			>
				Отправить
			</button>
		</div>

		<vue-editor v-model="content" :editor-toolbar="customToolbar"></vue-editor>
	</div>
</template>

<script>
	import { VueEditor } from 'vue2-editor'

	export default {
		name: 'Editor',

		data() {
			return {
				content: '',
				oldContent: '',
				contentHtml: null,
				isEditing: false,
				customToolbar: [
					['bold', 'italic', 'underline', 'strike'],
					['blockquote', 'code-block'],
					[{ list: 'bullet' }],
					[{ indent: '-1' }, { indent: '+1' }],
					[{ color: ['#17a2b8', '#b71a3b', '#f865bc', '#ddb089', '#ee9e59', '#6A6A6A', '#121212'] }],
					['image'],
					['clean'],
				]
			}
		},

		methods: {
			/**
			 */
			setContent(content) {
				if (this.content) {
					this.oldContent = this.content;
				}

				this.content = content;
				this.isEditing = true;
			},
			/**
			 */
			sendText() {
				if (this.content) {
					this.$emit('commit', this.content);
				}

				this.isEditing = false;
				this.content = '';
				this.contentHtml.innerHTML = '';

				if (this.oldContent) {
					this.content = this.oldContent;
					this.oldContent = '';
				}
			},
			/**
			 */
			cancelEditing() {
				this.isEditing = false;
				this.content = '';
				this.$emit('cancelEditing');

				if (this.oldContent) {
					this.content = this.oldContent;
					this.oldContent = '';
				}
			},
			/**
			 */
			handleKey(e) {
				if (e.ctrlKey && (e.keyCode === 0xA || e.keyCode === 0xD)) {
					this.sendText();
				}
			}
		},

		mounted() {
			this.contentHtml = this.$el.querySelector('.ql-editor');

			this.$el.addEventListener('keydown', (e) => this.handleKey(e));
			this.$emit('mounted');
		},

		beforeDestroy() {
			this.$el.removeEventListener('keydown', (e) => this.handleKey(e));
		},

		components: {
			VueEditor,
		},
	}
</script>

<style lang="less" scoped>
	@import "../css/_setters";

	.editor {
		position: relative;
		color: white;

		&__buttons {
			position: absolute;
			top: 11px;
			right: 0;

			button {
				margin-left: 5px;
			}
		}
	}
</style>



